import { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import LandingPage from './components/LandingPage';
import AuthModal from './components/AuthModal';
import PricingPage from './components/PricingPage';
import DocumentationPage from './components/DocumentationPage';
import DownloadsPage from './components/DownloadsPage';
import { AuthProvider } from './contexts/AuthContext';

function AppContent() {
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [authMode, setAuthMode] = useState<'login' | 'signup'>('login');
  const navigate = useNavigate();

  const handleGetStarted = () => {
    setAuthMode('signup');
    setShowAuthModal(true);
  };

  const handleLogin = () => {
    setAuthMode('login');
    setShowAuthModal(true);
  };

  const handleAuthSuccess = (token: string, email: string, username: string) => {
    // Store authentication data
    localStorage.setItem('medMasterToken', token);
    localStorage.setItem('medMasterEmail', email);
    localStorage.setItem('medMasterUsername', username);
    
    // Redirect to main app
    window.location.href = 'https://app.medmaster.site'; // Update this URL to your app domain
  };

  const handleCloseAuth = () => {
    setShowAuthModal(false);
  };

  // Navigation handlers
  const handleNavigateToPricing = () => {
    navigate('/pricing');
  };

  const handleNavigateToDocumentation = () => {
    navigate('/documentation');
  };

  const handleNavigateToDownloads = () => {
    navigate('/downloads');
  };

  const handleBackToLanding = () => {
    navigate('/');
  };

  return (
    <AuthProvider>
      <div className="App">
        <Routes>
          <Route 
            path="/" 
            element={
              <LandingPage 
                onGetStarted={handleGetStarted}
                onLogin={handleLogin}
                onNavigateToPricing={handleNavigateToPricing}
                onNavigateToDocumentation={handleNavigateToDocumentation}
                onNavigateToDownloads={handleNavigateToDownloads}
              />
            } 
          />
          <Route 
            path="/pricing" 
            element={<PricingPage onBackToLanding={handleBackToLanding} />} 
          />
          <Route 
            path="/documentation" 
            element={<DocumentationPage onBackToLanding={handleBackToLanding} />} 
          />
          <Route 
            path="/downloads" 
            element={<DownloadsPage onBackToLanding={handleBackToLanding} />} 
          />
        </Routes>
        
        {showAuthModal && (
          <AuthModal
            mode={authMode}
            onClose={handleCloseAuth}
            onAuthSuccess={handleAuthSuccess}
            onSwitchMode={(mode) => setAuthMode(mode)}
          />
        )}
      </div>
    </AuthProvider>
  );
}

function App() {
  return (
    <Router>
      <AppContent />
    </Router>
  );
}

export default App;